import axios from "axios";
import React, {FunctionComponent, useState} from "react";
import { CommentInterface } from "../interfaces";

export const CreateComment:FunctionComponent<any> = ({post, setFetchComments}) => {
    
    const [formData, setFormData] = useState<CommentInterface>({
		comment: "",
		contentType: "text/plain",
	} as CommentInterface);

    const submitData = () => {
		const user = JSON.parse(window.localStorage.getItem("user") || "{}");
		formData.author = user.id;

		if(!formData.comment || !formData.contentType){
			alert("Please fill out comment and contentType");
			return
		}
		
		axios.post(`${post.comments}/`, formData)
			.then(res => {
				console.log(res.data);
				setFormData({
					comment: "",
					contentType: "text/plain",
				} as CommentInterface)
				setFetchComments(true);
			})
			.catch(e => {
				const error = e.response.data;
				console.log(e);
				console.log(error)
			})
    }
    
    return ( 
        <div className="row my-3">
			<div className="col-md-7">
				<div className="form-group">
				<textarea
					className="form-control"
					id="comment"
					name="comment"
					value={formData.comment}
					onChange={(e) =>
					setFormData((prevFormData) => ({
						...prevFormData,
						comment: e.target.value,
					}))
					}
				/>
				</div>
			</div>
			<div className="col-md-3">
				<div className="form-group">
					<select
						className="form-control"
						id="contentType"
						name="contentType"
						value={formData.contentType}
						onChange={(e) =>
						setFormData((prevFormData) => ({
							...prevFormData,
							contentType: e.target.value,
						}))
						}
					>
						<option value="text/plain">Text/Plain</option>
						<option value="text/markdown">Text/Markdown</option>
					</select>
				</div>
			</div>
			<div className="col-md-2 my-auto">
				<button type="submit" className="btn btn-primary btn-block" onClick={submitData}>
					Submit
				</button>
			</div>
		</div>
        
    )
}